#front-page-hero {
	position: relative;
	z-index: 1;
	width: 100%;
	height: 30.625rem; // 490px
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	@include font-smooth;
	background-color: #fff;

	&:before {
		content: '';
		background-image: url('imgs/white-curve-down-lg.svg');
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		z-index: 10;
		background-size: 100% auto;
		background-position: 0 0;
		background-repeat: no-repeat;
	}

	&:after {
		content: '';
		background-image: url('imgs/white-curve-up-lg.svg');
		background-size: 100% auto;
		background-position: 0 100%;
		background-repeat: no-repeat;
		width: 100%;
		height: 100%;
		position: absolute;
		bottom: -2px;
		left: 0;
		right: 0;
		z-index: 10;
	}



	// @include respond-to('1150') {
	// 	height: 53.125rem; // 850px
	// }

	// @media (max-height: 669px) and (min-width: 1150px) {
	// 	height: 60vh;
	// }

	// @media (max-height: 800px) and (min-width: 1279px) {
	// 	height: 60vh;
	// }

	// @media (max-height: 768px) and (min-width: 1350px) {
	// 	height: 60vh;
	// }
}

.feature-wrapper {
	margin: 0;

	&:after {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(0,0,0,0.4);
		z-index: -1;
	}

	img {
		display: block;
		object-fit: cover;
		object-position: 70% 0;
		font-family: 'object-fit: cover';
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
	}
}

.hero-content {
	position: relative;
	z-index: 10;
	@include padding-left-right;
	@include font-smooth;

	.hero-title {
		display: block;
		font-family: $font__black;
		font-size: 2rem;
		color: #fff;
		line-height: 1;
		margin: 0 0 .75rem;
		text-transform: uppercase;
	}

	.hero-sub-title {
		display: block;
		font-size: 1.188rem;
		font-family: $font__med;
		color: #fff;
		margin: 0 0 2rem;
		line-height: 1.2;
		letter-spacing: .75px;
	}

	.btn {
		background-color: $color__brand-2;

		&:hover,
		&:focus {
			background-color: darken($color__brand-2, 5%) !important;
		}
	}
}